.message{
        width: 80%;
        height: 85vh;
        float: right;
        position: relative;
        overflow-y: scroll;
}
.message::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
    }
.message-input{
    width: 70vw;
    position: relative;
    left: 3vw;
}
.message-content{
    margin-bottom: 2vh;
    position: relative;
    left: 3vw;
    color: rgba(211, 214, 212, 0.8);
    font-size: 1.2vw;
    height: 10vh;
    width: 70vw;
    background-color: rgb(255, 255, 255,0.2);
}
.message-love{
    position: absolute;
    bottom: 0;
    right: 4px;
}
.message-content-text{
    margin-left: 4vw;
    color: rgb(87, 202, 208);
}
.message-content-user{
    font-size: 1.6vw;
    line-height: 1vw;
}
@media screen and (max-width:1000px){
    .message{
        width: 87vw;
        height: 70vh;
        float: none;
        position: relative;
        left: 8vw;
        overflow-y: scroll;
        margin-top: 0;
}
.message-input{
    width: 80vw;
    position: relative;
    left: 3vw;
}
.message-content{
    font-size: 3.2vw;
}
.message-content-text{
    margin-left: 10vw;
}
.message-content-user{
    font-size: 4vw;
    line-height: 3vw;
}
.message-content{
 width: 80vw;
}
}